body {
  font-family: Mulish, sans-serif;
  font-weight: 400;
  color: #000;
  position: relative;
  background-color: #fff; }

header {
  width: 100%; }

.container {
  width: 80%; }

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 6.66667vw;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.07);
  transition: .3s ease-in-out;
  position: fixed;
  top: 0%;
  animation: .6s ease-in-out showHeader forwards;
  color: #373F41;
  z-index: 100; }
  .header-button {
    display: flex;
    align-items: center; }
  .header-logo {
    display: flex;
    align-items: center; }
    .header-logo img {
      transition: .4s ease-in-out;
      width: 60%; }
    .header-logo span {
      font-weight: 800;
      font-size: 1.94444vw;
      transition: .4s ease-in-out;
      color: #0D1018; }
  .header-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px; }
  .header-nav {
    display: flex;
    align-items: center;
    width: 65%;
    justify-content: space-between; }
  .header.sticky {
    height: 5.27778vw; }
    .header.sticky .header-logo span {
      left: 0;
      transform: rotate(-90deg) translateY(-1000%);
      font-weight: 400;
      font-size: 0.55556vw; }
    .header.sticky .header-logo img {
      transform: translateX(20%); }

.primary-menu {
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .primary-menu .menu-item a {
    font-size: 1.18056vw;
    position: relative;
    font-weight: 400;
    color: inherit;
    transition: .3s ease-in-out;
    letter-spacing: .75px; }
    .primary-menu .menu-item a::after {
      content: '';
      height: 0.11111vw;
      width: 105%;
      position: absolute;
      bottom: 0;
      left: 50%;
      opacity: 0;
      transition: .3s ease-in-out;
      transform: translate(-50%, 15px);
      background-color: #82BB58; }
    @media (hover: hover) {
      .primary-menu .menu-item a:hover {
        color: #82BB58;
        text-shadow: 0 0 1px #82BB58, 0 0 1px #82BB58; } }
    @media (hover: hover) {
      .primary-menu .menu-item a:hover::after {
        opacity: 1;
        transform: translate(-50%, 5px); } }
  .primary-menu .menu-item:not(:last-child) {
    margin-right: 3.81944vw; }

.product-create {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #0D1018;
  width: 13.26389vw;
  height: 2.84722vw;
  border-radius: 40px;
  cursor: pointer;
  margin-right: 2.56944vw; }
  .product-create span {
    color: #F4F5F4;
    font-size: 1.11111vw; }

.product-cart {
  width: 3.05556vw;
  height: 3.05556vw;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  background-color: #F4F5F4;
  cursor: pointer; }
  .product-cart .cart-btn {
    outline: none;
    width: 50%;
    border: none;
    pointer-events: none; }
  .product-cart .cart-num {
    position: absolute;
    width: 1.52778vw;
    height: 1.52778vw;
    top: 0;
    right: 0;
    transform: translateX(50%);
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #F57C1F;
    border-radius: 50%;
    font-size: 1.04167vw;
    font-weight: 600;
    color: #fff; }
